/* eslint-disable no-unused-vars */
import {
    takeLatest, put, call, delay,
} from 'redux-saga/effects'
import axios from 'axios'
import * as actions from '../actions'

import helpers from '../../helpers'

const { GET_VALSMARKETS } = helpers.api

const {
    GraphActions: {
        default: {
            setBriefcaseData,
        },
    },
} = actions

function* setUser(...args) {
    yield delay(500)
    const {
        start, end,
    } = yield args[0]
    try {
        const res = yield call(axios, {
            method: 'get',
            url: GET_VALSMARKETS,
            params: {
                dateStart: start,
                dateEnd: end,
            },
            withCredentials: true,
        })
        return yield put(setBriefcaseData(res.data))
    } catch (error) {
        // eslint-disable-next-line no-console
        console.error(error)
    }
    return null
}

function* watchData() {
    yield takeLatest(actions.GraphActions.GET_BRIEFCASE_DATA, setUser)
}

export default {
    watchData,
}
