import Actions from './instance'

const { action } = Actions

export const OPEN_MODAL = 'OPEN_MODAL'
export const CLOSE_MODAL = 'CLOSE_MODAL'


const openModal = name => action(OPEN_MODAL, {
    name,
})

const closeModal = name => action(CLOSE_MODAL, {
    name,
})

export default {
    openModal,
    closeModal,
}
