import * as actions from '../actions'

const initialState = {
    names: [],
}

const auth = (state = initialState, action) => {
    switch (action.type) {
        case actions.ModalsActions.OPEN_MODAL:
            return Object.assign({}, {
                ...state,
                names: [...state.names, action.name],
            })
        case actions.ModalsActions.CLOSE_MODAL:
            return Object.assign({}, {
                ...state,
                names: state.names.filter(i => i !== action.name),
            })
        default:
            return Object.assign({}, state)
    }
}

export default auth
