import Actions from './instance'

const { action } = Actions

export const GET_BRIEFCASE_DATA = 'GET_BRIEFCASE_DATA'
export const SET_BRIEFCASE_DATA = 'SET_BRIEFCASE_DATA'

const getBriefcaseData = ({ start, end }) => action(GET_BRIEFCASE_DATA, {
    start, end,
})

const setBriefcaseData = ({
    CurrencyesMarket, StockMarket, StructureProduct, UrgentMarket,
}) => action(SET_BRIEFCASE_DATA, {
    CurrencyesMarket,
    StockMarket,
    StructureProduct,
    UrgentMarket,
})

export default {
    getBriefcaseData,
    setBriefcaseData,
}
