// eslint-disable-next-line no-unused-vars
import React, { useState, useEffect } from 'react'
import { connect } from 'react-redux'

import Loader from '../../components/layouts/Loader'

import * as actions from '../../redux/actions'
import helpers from '../../helpers'

const { linksPaths } = helpers.constants.links

function PrivateRouteComponent(props) {
    const {
        children, loggedIn, isAuth, username, password, checked,
    } = props

    isAuth(username, password)

    useEffect(() => {
        if (props.location.pathname !== linksPaths.root && !loggedIn) {
            props.navigate(linksPaths.root)
        }
        if (props.location.pathname === linksPaths.root && loggedIn) {
            props.navigate(linksPaths.briefcase)
        }
    })

    return checked ? children : <Loader />
}

const ConnectedPrivateRouteComponent = connect(
    state => ({
        username: state.auth.username,
        password: state.auth.password,
        loggedIn: state.auth.loggedIn,
        checked: state.auth.checked,
    }),
    dispatch => ({
        isAuth: (username, password) =>
            dispatch(actions.AuthActions.default.isAuthentithicated({ username, password })),
    }),
)(PrivateRouteComponent)

export default ConnectedPrivateRouteComponent
