import React from 'react'
import { Button, Header, Modal } from 'semantic-ui-react'
import axios from 'axios'

import logoText from '../../../../images/logoText.svg'

import helpers from '../../../../helpers'

const { POST_GET_LOGOUT } = helpers.api
const { root } = helpers.constants.links.linksPaths
const { Exit: ExitModalName } = helpers.constants.modals

const Exit = ({ open, closeModal }) => {
    const defaultProps = {
        open,
        closeOnDimmerClick: true,
    }

    const onExit = () => {
        axios.post(POST_GET_LOGOUT)
            .then(() => {
                closeModal('Exit')
                window.location.replace(root)
            })
            // eslint-disable-next-line no-console
            .catch(e => console.error(e))
    }

    return (
        <Modal {...defaultProps} closeOnDimmerClick onClose={() => closeModal(ExitModalName)} >
            <Header>
                <div className={Header.logo}>
                    <img src={logoText} alt="logo" />
                </div>
            </Header>
            <Modal.Content>
                <p>   ?</p>
            </Modal.Content>
            <Modal.Actions>
                <Button className="button" onClick={onExit}>
                    
                </Button>
                <Button className="button" onClick={() => closeModal(ExitModalName)}>
                    
                </Button>
            </Modal.Actions>
        </Modal>
    )
}

export default Exit
