/* eslint-disable no-unused-vars */
import {
    takeLatest, put, call,
} from 'redux-saga/effects'
import axios from 'axios'
import * as actions from '../actions'

const {
    TableActions: {
        default: {
            getTable,
            receiveTable,
        },
    },
} = actions

function* fetchTable(...args) {
    const {
        tableName, url, page, sort, order, perPage, type,
    } = yield args[0]
    try {
        const res = yield call(axios, {
            method: 'get',
            url,
            params: {
                page,
                sort,
                order,
                perPage,
            },
            withCredentials: true,
        })
        return yield put(receiveTable(tableName, res.data))
    } catch (error) {
        // eslint-disable-next-line no-console
        console.error(error)
    }
    return null
}

function* watchFetchTable() {
    yield takeLatest(actions.TableActions.GET_TABLE, fetchTable)
}

export default {
    watchFetchTable,
}
