import * as actions from '../actions'

const initialState = {
    username: null,
    password: null,
    loggedIn: false,
    checked: false,
}

const auth = (state = initialState, action) => {
    switch (action.type) {
        case actions.AuthActions.IS_AUTHENTITHICATED:
        case actions.AuthActions.ASK_AUTH:
            return Object.assign({}, {
                ...state,
                username: action.username,
                password: action.password,
            })
        case actions.AuthActions.SET_AUTHENTITHICATED:
            return Object.assign({}, {
                ...state,
                loggedIn: action.loggedIn,
                checked: true,
            })
        default:
            return Object.assign({}, state)
    }
}

export default auth
