import React, { useRef, useState } from 'react'
import { Button, Header, Modal } from 'semantic-ui-react'

import helpers from '../../../../helpers'

const { FreeOrder: FreeOrderModalName } = helpers.constants.modals

const FreeOrder = ({ open, closeModal }) => {
    const defaultProps = {
        open,
        closeOnDimmerClick: true,
    }

    const [status, setStatus] = useState(0)

    const file = useRef(null)

    return (
        <Modal {...defaultProps} closeOnDimmerClick onClose={() => closeModal(FreeOrderModalName)}>
            {
                status === 0
                    ? (
                        <React.Fragment>
                            <Header className="header_noBg">
                                <button className="chrest chrest_black" onClick={() => closeModal(FreeOrderModalName)} type="button">
                                    <span />
                                    <span />
                                </button>
                            </Header>
                            <Modal.Content>
                                <p className="centerMyText">
                                        
                                    <br />
                                    {' '}
                                      
                                </p>
                            </Modal.Content>
                            <Modal.Actions className="actions_center">
                                <input className="hiddenTHing" type="file" ref={file} onChange={() => setStatus(1)} />
                                <Button className="button" onClick={() => file.current.click()}>
                                    
                                </Button>
                            </Modal.Actions>
                        </React.Fragment>
                    )
                    : (
                        <React.Fragment>
                            <Header className="header_noBg">
                                <button className="chrest chrest_black" onClick={() => closeModal(FreeOrderModalName)} type="button">
                                    <span />
                                    <span />
                                </button>
                            </Header>
                            <Modal.Content>
                                <p className="centerMyText">
                                      
                                </p>
                            </Modal.Content>
                            <Modal.Actions className="actions_center">
                                <Button className="button" onClick={() => closeModal(FreeOrderModalName)}>
                                    
                                    </Button>
                            </Modal.Actions>
                        </React.Fragment>
                    )
            }
        </Modal>
    )
}

export default FreeOrder
