import Actions from './instance'

const { action } = Actions

export const IS_AUTHENTITHICATED = 'IS_AUTHENTITHICATED'
export const ASK_AUTH = 'ASK_AUTH'
export const SET_AUTHENTITHICATED = 'SET_AUTHENTITHICATED'

const isAuthentithicated = ({ username, password }) => action(IS_AUTHENTITHICATED, {
    username, password,
})

const setAuthentithicated = loggedIn => action(SET_AUTHENTITHICATED, {
    loggedIn,
})

const askAuth = ({ username, password }) => action(ASK_AUTH, {
    username, password,
})

export default {
    isAuthentithicated,
    setAuthentithicated,
    askAuth,
}
