/* eslint-disable no-unused-vars */
import {
    takeEvery, takeLatest, put, call, select,
} from 'redux-saga/effects'
import axios from 'axios'
import * as actions from '../actions'

import helpers from '../../helpers'

const { GET_ME, POST_GET_AUTH } = helpers.api

const {
    AuthActions: {
        default: {
            isAuthentithicated,
            setAuthentithicated,
            askAuth,
        },
    },
} = actions

function* fetchUser(...args) {
    const {
        username, password,
    } = yield args[0]
    try {
        const res = yield call(axios, {
            method: 'get',
            url: GET_ME,
            withCredentials: true,
        })
        return yield put(setAuthentithicated(true))
    } catch (error) {
        // eslint-disable-next-line no-console
        console.error(error)
    }
    return yield put(setAuthentithicated(false))
}

function* setUser(...args) {
    const {
        username, password,
    } = yield args[0]
    try {
        const res = yield call(axios, {
            method: 'post',
            url: POST_GET_AUTH,
            data: {
                login: username,
                password,
            },
            withCredentials: true,
        })
        return yield put(setAuthentithicated(true))
    } catch (error) {
        // eslint-disable-next-line no-console
        console.error(error)
    }
    return yield put(setAuthentithicated(false))
}

function* watchUsers() {
    yield takeEvery(actions.AuthActions.IS_AUTHENTITHICATED, fetchUser)
    yield takeLatest(actions.AuthActions.ASK_AUTH, setUser)
}

export default {
    watchUsers,
}
