import * as actions from '../actions'
import helpers from '../../helpers'

const { getAxisX, getAxisY, getMyValues } = helpers.utils.graph

const identifierInterestValMain = ' '

const valueFieldCurrency = ' ()'
const valueInterestCurrency = 'T1'

const valueFieldStockUrg = ' ()'
const valueInterestStock = 'T2'

const valueFieldStructure = '  ()'
const valueInterestStructure = '*'

const valueInterestUrgent = 'T0'

const initialState = {
    CurrencyesMarket: [],
    StockMarket: [],
    StructureProduct: [],
    UrgentMarket: [],
    CurrencyMarketInfo: {},
    StockMarketInfo: {},
    StructureProductInfo: {},
    UrgentMarketInfo: {},
    axisX: [],
}

const graph = (state = initialState, action) => {
    switch (action.type) {
        case actions.GraphActions.GET_BRIEFCASE_DATA:
            return Object.assign({}, {
                ...state,
                start: action.start,
                end: action.end,
            })
        case actions.GraphActions.SET_BRIEFCASE_DATA:
            return Object.assign({}, {
                ...state,
                CurrencyesMarket: action.CurrencyesMarket,
                StockMarket: action.StockMarket,
                StructureProduct: action.StructureProduct,
                UrgentMarket: action.UrgentMarket,

                CurrencyMarketInfo: getMyValues(
                    action.CurrencyesMarket,
                    valueFieldCurrency,
                    identifierInterestValMain,
                    valueInterestCurrency,
                ),
                StockMarketInfo: getMyValues(
                    action.StockMarket,
                    valueFieldStockUrg,
                    identifierInterestValMain,
                    valueInterestStock,
                ),
                StructureProductInfo: getMyValues(
                    action.StructureProduct,
                    valueFieldStructure,
                    identifierInterestValMain,
                    valueInterestStructure,
                ),
                UrgentMarketInfo: getMyValues(
                    action.UrgentMarket,
                    valueFieldStockUrg,
                    identifierInterestValMain,
                    valueInterestUrgent,
                ),

                axisX: getAxisX([
                    getMyValues(
                        action.CurrencyesMarket,
                        valueFieldCurrency,
                        identifierInterestValMain,
                        valueInterestCurrency,
                    ),
                    getMyValues(
                        action.StockMarket,
                        valueFieldStockUrg,
                        identifierInterestValMain,
                        valueInterestStock,
                    ),
                    getMyValues(
                        action.StructureProduct,
                        valueFieldStructure,
                        identifierInterestValMain,
                        valueInterestStructure,
                    ),
                    getMyValues(
                        action.UrgentMarket,
                        valueFieldStockUrg,
                        identifierInterestValMain,
                        valueInterestUrgent,
                    ),
                ]),
                axisY: getAxisY(
                    getAxisX([
                        getMyValues(
                            action.CurrencyesMarket,
                            valueFieldCurrency,
                            identifierInterestValMain,
                            valueInterestCurrency,
                        ),
                        getMyValues(
                            action.StockMarket,
                            valueFieldStockUrg,
                            identifierInterestValMain,
                            valueInterestStock,
                        ),
                        getMyValues(
                            action.StructureProduct,
                            valueFieldStructure,
                            identifierInterestValMain,
                            valueInterestStructure,
                        ),
                        getMyValues(
                            action.UrgentMarket,
                            valueFieldStockUrg,
                            identifierInterestValMain,
                            valueInterestUrgent,
                        ),
                    ]),
                    [
                        getMyValues(
                            action.CurrencyesMarket,
                            valueFieldCurrency,
                            identifierInterestValMain,
                            valueInterestCurrency,
                        ),
                        getMyValues(
                            action.StockMarket,
                            valueFieldStockUrg,
                            identifierInterestValMain,
                            valueInterestStock,
                        ),
                        getMyValues(
                            action.StructureProduct,
                            valueFieldStructure,
                            identifierInterestValMain,
                            valueInterestStructure,
                        ),
                        getMyValues(
                            action.UrgentMarket,
                            valueFieldStockUrg,
                            identifierInterestValMain,
                            valueInterestUrgent,
                        ),
                    ],
                ),
            })
        default:
            return Object.assign({}, state)
    }
}

export default graph
