import React from 'react'
import { connect } from 'react-redux'

import Modals from '../../components/common/Modals'

import * as actions from '../../redux/actions'

import './Dimmer.scss'

function ModalConductor(props) {
    const { modals, closeModal, children } = props
    return (
        <React.Fragment>
            {children}
            {
                modals.map((i) => {
                    const ModalComponent = Modals[i]
                    return <ModalComponent key={i} open closeModal={closeModal} />
                })
            }
        </React.Fragment>
    )
}

const ConnectedModalConductor = connect(
    state => ({
        modals: state.modals.names,
    }),
    dispatch => ({
        closeModal: name => dispatch(actions.ModalsActions.default.closeModal(name)),
    }),
)(ModalConductor)

export default ConnectedModalConductor
